<template>
  <div class="fullpage">
    <div id="dowebok" v-on:click="a=!a">
      <div class="section">
        <div class="slide" v-for="(item, key) in words">
            <div>
                <span class="num">{{key}}</span>
                <label :style="{color: getRandColor()}">{{item.en}}<br/><span style="font-family: cursive;">{{item.zh}}</span></label>
            </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  data () {
    return {
        a: false,
        words: []
    }
  },
  mounted: function() {
    let vm = this
    this.$ajax.get('./api/words').then(function(response) {
        vm.words = response.data.slice(0,100)

        $(function(){
        $('#dowebok').fullpage({
            verticalCentered: true,
            continuousHorizontal: true,
            keyboardScrolling: true,
            touchSensitivity: 3,
            css3: true,
            slidesNavigation: false,
            scrollingSpeed: 1000,
            resize: true,
            slidesNavPosition: 'top',
            loopHorizontal: true,
        });
        let i = 0
        let left = false
        setInterval(function(){
            if (!vm.a) {
                if (left) {
                    $.fn.fullpage.moveSlideLeft();
                } else {
                    $.fn.fullpage.moveSlideRight();
                }
                if (i++ === 99) {
                    i = 0
                    left = !left
                }
                // $.fn.fullpage.moveTo(3);
            }
        }, 5000);   
        });
    })
  },
  methods: {
    getRandColor() {
        return '#' + (~~(Math.random() * Math.pow(2, 24))).toString(16)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">

#dowebok {
    outline: 1px solid red;

    .section {
        text-align: center;

        .slide {
            font-size: 10rem;
            background-color: black;
            color: #337ab7;
            
            label {
                transform: rotateZ(00deg);
            }
            
            span {
            }
            
            .num {
                color: #0aff14;
                font-family: monospace;
                font-size: 8rem;
                display: block;
            }
        }
    }
}
</style>



